import wechatIcon from '../assets/images/icon_pay_wechat.png';
// import addIcon from '../assets/images/icon_add.png';
import xxzfIcon from '../assets/images/icon_xxzf01@2x.png';
import { previewOrder, submitOrder } from '@/api/index.js';

export default {
  name: 'comfirmOrder',
  props: ['merchandiseId', 'payType', 'payId', 'installNum'],
  data() {
    return {
      selected: 1,
      showBillDay: false,
      showPop: false,
      showPayway: false,
      icons: {
        wechatIcon,
        xxzfIcon,
      },
      bankcard: this.$store.state.bankcard,
      initData: {
        merchandise: {},
        order: {},
      },
      billDay: '10',
      areaList: {
        province_list: {
          1: '1日',
          2: '2日',
          3: '3日',
          4: '4日',
          5: '5日',
          6: '6日',
          7: '7日',
          8: '8日',
          9: '9日',
          10: '10日',
          11: '11日',
          12: '12日',
          13: '13日',
          14: '14日',
          15: '15日',
          16: '16日',
