          18: '18日',
          19: '19日',
          20: '20日',
          21: '21日',
          22: '22日',
          23: '23日',
          24: '24日',
          25: '25日',
        },
        postData: null,
        // isrenew: null,
      },
    };
  },
  mounted() {
    this.postData = {
      merchandiseId: this.$route.params.merchandiseId,
      payType: this.$route.params.payType,
      payId: this.$route.params.payId,
      installNum:
        this.$route.params.installNum === '-1'
          ? ''
          : this.$route.params.installNum,
      bankCardId: this.bankcard.bankCardId,
    };
    this.queryPreviewOrder();
    // this.isrenew = this.$route.params.payType == 500101;
  },
  computed: {},
  created() {},
  methods: {
    changeBillDay(obj) {
      this.showBillDay = false;
      this.billDay = obj[0].code;
    },
    isrenew() {
      if (this.$route.params.payType === '500101') {
        return true;
      }
      return false;
    },
    queryPreviewOrder() {
      // this.$route.params.merchandiseId,
      // this.$route.params.payType,
      // this.$route.params.payId,
      // this.$route.params.installNum == -1
      //   ? ''
      //   : this.$route.params.installNum,
      // this.bankcard.bankCardId,
      previewOrder(this.postData)
        .then((result) => {
          if (result) {
            this.initData = result;
            // console.log(this.initData.merchandise.name);
          }
        })
        .catch(err => console.error(err));
    },
    toSubmitOrder() {
      // const obj = {
      //   // token:'token',
      //   merchandiseId: this.$route.params.merchandiseId,
      //   payType: this.$route.params.payType,
      //   payId: this.$route.params.payId,
      //   installNum:
      //     this.$route.params.installNum == -1
      //       ? ""
      //       : this.$route.params.installNum,
      //   billDay: this.billDay,
      //   bankCardId: this.bankcard.bankCardId
      // };
      const obj = this.postData;
      obj.billDay = this.billDay;
      submitOrder(obj)
        .then(() => {
          this.$router.push({
            path: '/pay-calculated',
          });
        })
        .catch(err => console.error(err));
    },
  },
  components: {},
};
</script>
